import signUp from './nologin.vue'
import userSidebar from './userSidebar.vue'

export default{
  data() {
    return{
      height: 0,
      changeLeft: false
    }
  },
  methods: {
      onClickingMask: function () {
          this.changeLeft = false
      }
  },
  components:{
    signUp,
    userSidebar
  },
  mounted: function () {
    this.height = window.innerHeight
  }
}



