
import Masker from 'vux/src/components/masker/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import sign from './sidebar/sidebar.vue'
import state from './Loading/stateLine.vue'
import {go} from '../libs/router'
import {isEmptyObject} from '../libs/isEmptyObject'
import {mapGetters} from 'vuex'
import axios from 'axios'

export default{
  data() {
    return {
      activeColor: {
        color: '#FF005A'
      },
      showsidebar: false,
      loading: false,
      error: false,
      empty: false,
      collectionId: 1
    }
  },
  computed: {
    ...mapGetters({
      items: 'GetSpecialList'
    })
  },
  components: {
    Masker,
    sign,
    Scroller,
    state
  },
  mounted() {
    this.$http.get('/userinfo',{
      headers: {
        'x-access-token': window.localStorage.token
      }
    })
      .then(data => {
        this.$store.commit('getUserInfo', data.data.result)
      })
  },
  methods: {
    goSpecial(id) {
      go({name: 'special', params: {id: id}}, this.$router)
    },
    refresh() {
      var _this = this
      this.$store.dispatch('getSpecialAction').then(() => {
        setTimeout(function () {
          _this.$refs.scroller.donePulldown()
        }, 1000)
        _this.$nextTick(() => {
          _this.$refs.scroller.reset()
        })
      })
    },
    ready() {
      this.loading = true
      this.error = false
      this.empty = false
      this.$store.dispatch('clearSpecialAction')
      var _this = this
      setTimeout(function () {
        _this.$store.dispatch('getSpecialAction').then((data) => {
          _this.$nextTick(() => {
            _this.$refs.scroller.reset()
          })
          if (isEmptyObject(data)) _this.empty = true
          _this.loading = false
          _this.error = false
        }, (err) => {
          _this.loading = false
          _this.error = true
        })
      }, 2000)
    },
    showSidebar() {
      this.showsidebar = !this.showsidebar
      this.$refs.sidebar.changeLeft = !this.$refs.sidebar.changeLeft
    },
    upload() {
      let _this = this
      if (this.$store.state.user.token == '') {
        this.$vux.toast.show({
          text: '',
          position: 'default',
          type: 'cancel',
          onHide() {
            go({name: 'login'}, _this.$router)
          }
        })
      } else {
        go({name: 'upload'}, this.$router)
      }
    },
    collection(id) {
      let _this = this
      if (this.$store.state.user.token == '') {
        this.$vux.toast.show({
          text: '',
          position: 'default',
          type: 'cancel',
          onHide() {
            go({name: 'login'}, _this.$router)
          }
        })
      }else {
        console.log(window.localStorage.token)
         this.$http.get('/collecion',{
            headers: {
              'x-access-token': window.localStorage.token
            },
            params: {
              id: id
            }
          }).then(data => {
            this.collectionId = data.data.result.Ok
            this.$store.commit('collection', data.data.result.Ok)
            if (data.data.result.Ok == 0) {
              this.$vux.toast.show({
              text: '',
              position: 'default',
              type: 'success',
              })
            }else {
              this.$vux.toast.show({
              text: '',
              position: 'default',
              type: 'success',
              })
            }
         }, () => {
          this.$vux.toast.show({
          text: '',
          position: 'default',
          type: 'cancel',
          onHide() {
          }
        })
         })
      }

    }
  }
}



