import Loading from './Loading.vue'

export default{
    components: {
        Loading
    },
    props: {
      loading: {
        type: Boolean,
        default: false
      },
      error: {
        type: Boolean,
        default: false
      },
      empty: {
        type: Boolean,
        default: false
      }
    },
    mounted: function () {
      this.refresh()
    },
    methods: {
        refresh: function () {
            this.$emit('loadData')
        }
    }

}




